import React, { Component } from 'react';
import Star from "../component/star.js"
import { Table, Container, Row } from "react-bootstrap";
import { FaStar } from 'react-icons/fa'


class Board extends Component {
    constructor(props) {
      super(props);
      this.state = {
        squares: Array(6).fill(0).map(row => new Array(7).fill(0)),
        xIsNext: true,
        color : {1:"#f56363", 2: "#4f1de8a8", 0: null},
        winner : {1:"Player-1", 2:"PLayer-2", 4:"Nobody! Play Again! :)"}
      };
    }
  
    handleClick(i,j) {
      const squares = this.state.squares.slice();

      if (squares[i][j] != 0 || calculateWinner(this.state.squares)>0) {
        return;
      }
      
      while (i + 1 < 6 && squares[i + 1][j] == 0) {
            i++;
      }

      squares[i][j] = this.state.xIsNext ? 1 : 2;
      this.setState({
        squares: squares,
        xIsNext: !this.state.xIsNext,
      });
    }
  
    renderSquare(i,j) {
      return (
        <Star
          onClick={() => this.handleClick(i,j)}
          value = {this.state.color[this.state.squares[i][j]]}
        />
      );
    }
  
    render() {
      const winner = calculateWinner(this.state.squares);
      let status;
      if (winner > 0) {
        //status = 'Winner: ' + this.state.winner[winner];
        if(winner===1){
            status = <h3>WINNER: <FaStar size = "6vw" color = "#f56363" /> </h3>
        }
        else if(winner===2){
            status = <h3>WINNER: <FaStar size = "6vw" color = "#4f1de8a8"  /> </h3>
        }
        else{
            status = <h3> WINNER: Nobody! Play Again! :) </h3>
        }
        
      } else {
        //status = 'Next player: ' + (this.state.xIsNext ? <FaStar size = "4vw" color = "#f56363" /> : <FaStar size = "4vw" color = "#4f1de8a8" />);
        status =  (this.state.xIsNext ? <h3>Next player: <FaStar size = "6vw" color = "#f56363" /></h3> : <h3>Next player: <FaStar size = "6vw" color = "#4f1de8a8" /></h3>);

      }
  
      return (
        <Container sm>
          <Row>
          <div className="status"><h3>{status}</h3></div>  
          <Table bordered variant = "info">
            <tbody>
                <tr>
                    {this.renderSquare(0,0)}
                    {this.renderSquare(0,1)}
                    {this.renderSquare(0,2)}
                    {this.renderSquare(0,3)}
                    {this.renderSquare(0,4)}
                    {this.renderSquare(0,5)}
                    {this.renderSquare(0,6)}
                </tr>
                <tr>
                    {this.renderSquare(1,0)}
                    {this.renderSquare(1,1)}
                    {this.renderSquare(1,2)}
                    {this.renderSquare(1,3)}
                    {this.renderSquare(1,4)}
                    {this.renderSquare(1,5)}
                    {this.renderSquare(1,6)}
                </tr>
                <tr>
                    {this.renderSquare(2,0)}
                    {this.renderSquare(2,1)}
                    {this.renderSquare(2,2)}
                    {this.renderSquare(2,3)}
                    {this.renderSquare(2,4)}
                    {this.renderSquare(2,5)}
                    {this.renderSquare(2,6)}
                </tr>
                <tr>
                    {this.renderSquare(3,0)}
                    {this.renderSquare(3,1)}
                    {this.renderSquare(3,2)}
                    {this.renderSquare(3,3)}
                    {this.renderSquare(3,4)}
                    {this.renderSquare(3,5)}
                    {this.renderSquare(3,6)}
                </tr>
                <tr>
                    {this.renderSquare(4,0)}
                    {this.renderSquare(4,1)}
                    {this.renderSquare(4,2)}
                    {this.renderSquare(4,3)}
                    {this.renderSquare(4,4)}
                    {this.renderSquare(4,5)}
                    {this.renderSquare(4,6)}
                </tr>
                <tr>
                    {this.renderSquare(5,0)}
                    {this.renderSquare(5,1)}
                    {this.renderSquare(5,2)}
                    {this.renderSquare(5,3)}
                    {this.renderSquare(5,4)}
                    {this.renderSquare(5,5)}
                    {this.renderSquare(5,6)}
                </tr>
            </tbody>
           </Table>
           </Row>
         </Container>
      );
    }
  }

  function calculateWinner(data) {
    var cnt=0;
    for (var row = 5; row >= 0; row--) {
        for (var col = 0; col < 7; col++) {
            //check for each direction for all values
            //if value is zero, skip it
            if (data[row][col] == 0) {
                cnt++;
                continue;
            }
            //if 1 or 2, continue to check in all 5 directions
            else {
                var d = data[row][col];
                //1.upwards
                if(row>2){
                    if (data[row - 1][col] == d && data[row - 2][col] == d && data[row - 3][col] == d) {
                        return d;
                    }
                }
                
                //2.left
                if (col - 3 >= 0) {
                    if (data[row][col - 1] == d && data[row][col - 2] == d && data[row][col - 3] == d) {
                        return d;
                    }
                }
                //3.right
                if (col + 3 <= 6) {
                    if (data[row][col + 1] == d && data[row][col + 2] == d && data[row][col + 3] == d) {
                        return d;
                    }
                }
                //4.diagonal left
                if (row>2 && row - 3 >= 0 && col - 3 >= 0) {
                    if (data[row - 1][col - 1] == d && data[row - 2][col - 2] == d && data[row - 3][col - 3] == d) {
                        return d;
                    }
                }
                //5.diagonal right
                if (row>2 && row - 3 <= 6 && col + 3 <= 6) {
                    if (data[row - 1][col + 1] == d && data[row - 2][col + 2] == d && data[row - 3][col + 3] == d) {
                        return d;
                    }
                }
            }
        }
    }

    if(cnt>0) return 0;
    return 4;
  }

  export default Board;