import { FaRegStar, FaStar } from 'react-icons/fa'
import React, { Component } from 'react';
import { TiStar } from "react-icons/ti";

class Star extends Component {
    render() {
        if (this.props.value != null) {
            return <td style={{textAlign: 'center', cursor: 'pointer'}} onClick={this.props.onClick} ><h4><FaStar size = "4vw" color = {this.props.value} /></h4></td>;
        }
        return <td style={{textAlign: 'center', cursor: 'pointer'}} onClick={this.props.onClick} ><h4><FaRegStar size = "4vw"/></h4></td>;
    }
} 

export default Star